<template>
  <div>
    <!-- 表格 -->
    <el-card class="box-card">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>打印参数列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增打印参数弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="620px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="130px"
      >
        <el-form-item label="模板类型" prop="file_type">
          <el-input v-model="dialogForm.file_type"></el-input>
        </el-form-item>

        <el-form-item label="" prop="file">
          <el-upload
            class="upload-demo"
            :auto-upload="false"
            :action="UploadUrl()"
            accept=".txt"
            :before-upload="beforeUploadFile"
            :before-remove="beforeRemove"
            :on-change="fileChange"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            multiple
            :limit="limitNum"
            :on-exceed="handleExceed"
            :file-list="fileList"
          >
            <el-button size="small" type="primary">上传文件</el-button>
            <span class="el-upload__tip" slot="tip">
              只能上传txt文件，且不超过10M
            </span>
          </el-upload>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import axios from "axios";

export default {
  name: "UserList",
  components: {
    ELTable,
  },
  //   watch: {
  //     CurrentUserInfo: function (val, oldVal) {
  //       //当val变更之后在执行接下来的操作
  //       console.log(" val val val val val",val);
  //       console.log(" oldVal oldVal oldVal oldVal oldVal",oldVal);
  //     },
  //   },
  data() {
    return {
      limitNum: 1, // 上传excell时，同时允许上传的最大数
      fileList: [], // excel文件列表
      // 筛选
      listQuery: {
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增打印参数弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        file_type: "",
        file: "",
      },
      dialogFormBase: {
        file_type: "",
        file: "",
      },
      dialogFormRules: {
        file_type: [
          { required: true, message: "请选择模板类型", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "模板类型",
          field: "Supplir",
          align: "center",
        },
        {
          label: "文件名称",
          field: "Rve",
          align: "center",
        },
        {
          label: "上传时间",
          field: "Rev",
          align: "center",
        },
     
        {
          label: "操作",
          colType: "solt",
          align: "center",
          width: 150,
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState(["WorkCodeNameList", "CurrentUserInfo"]),
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    UploadUrl: function () {
      // 因为action参数是必填项，我们使用二次确认进行文件上传时，直接填上传文件的url会因为没有参数导致api报404，所以这里将action设置为一个返回为空的方法就行，避免抛错
      return "";
    },
    // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== "txt") {
        this.$message({
          message: "只能上传后缀是.txt的文件",
          type: "warning",
        });
        return false;
      }
      if (size > 10) {
        this.$message({
          message: "文件大小不得超过10M",
          type: "warning",
        });
        return false;
      }
    },
    fileChange(file, fileList) {
      console.log(file.raw);
      this.fileList.push(file.raw);
      console.log(this.fileList);
    },
    handleRemove(file, fileList) {
      console.log(11111, file, fileList);
      this.fileList = fileList;
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 ${this.limitNum} 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 打印参数列表 查询
    getLogisticOperationSystem(params) {
      //   this.tableData = [
      //     {
      //       Package_Qty: "20",
      //       Rv: "00A01",
      //       Itemcode_C_Shipping: "SUB-ASSHEUH-2A001364-100",
      //       Supplier: "SPR001300",
      //       No_Ship: "40",
      //       CS_type: "SPR001300-2A001364-CMSS-A01-081021-0001",
      //       Shipping_SN_length: "11",
      //     },
      //   ];

      baseManagement.getLogisticOperationSystem(params).then((res) => {
        console.log("打印参数列表 查询", res);
        if (res.code == 0) {
          this.tableData = res.data.data;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 打印参数列表 新增
    postLogisticOperationSystem(params) {
      baseManagement.postLogisticOperationSystem(params).then((res) => {
        console.log("打印参数列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 打印参数列表 新增
    addBtn() {
      this.dialogTitle = "新增打印参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 打印参数列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改打印参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.dialogForm = {
        ID: rowData.ID,
        file_type: rowData.file_type,
        file: rowData.file,
      };
    },

    // 打印参数列表 修改
    getLogisticPutOperationSystem(params) {
      baseManagement.getLogisticPutOperationSystem(params).then((res) => {
        console.log("打印参数列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增打印参数") {
            console.log("新增打印参数", this.dialogForm);

            console.log(this.fileList.length, this.fileList);
            console.log(this.fileList.length, this.fileList[0]);
            if (this.fileList.length === 0) {
              this.$message.warning("请上传文件");
            } else {
              let form = new FormData();
              form.append("file", this.fileList[0]);
              form.append("fileType", "物料编码");
              console.log("form", form);

              axios({
                url: "/LogisticAddModel",
                method: "post",
                data: form,
                traditional: true,
                headers: { "Content-Type": "multipart/form-data" },
                // responseType: 'arraybuffer',
              }).then((res) => {
               
                if (res.data.code == 0) {
                  this.$message({
                    message: res.data.message,
                    type: "success",
                  });
                } else {
                  this.$message({
                    message: res.data.message,
                    type: "error",
                  });
                }
              });
            }

          } else if (this.dialogTitle == "修改打印参数") {
            console.log("修改打印参数", this.dialogForm);
            this.getLogisticPutOperationSystem(this.dialogForm);
          }
        }
      });
    },

    // 打印参数列表 删除
    getLogisticDeleteOperationSystem(params) {
      baseManagement.getLogisticDeleteOperationSystem(params).then((res) => {
        console.log("打印参数列表 删除", res);
        if (res.code == 0) {
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteOperationSystem({ ID: rowData.ID });
      });
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem(this.listQuery);
  },
};
</script>

